.dashboard-container {
    width: 90%;
    margin: 20px auto;
    font-family: Arial, sans-serif;
    background: #f8fafc;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.dashboard-title {
    font-size: 1.5rem;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
    color: #1e293b;
}

.dashboard-table {
    width: 100%;
    border-collapse: collapse;
    background: white;
    border-radius: 8px;
    overflow: hidden;
}

.dashboard-table th,
.dashboard-table td {
    padding: 12px 16px;
    text-align: center;
    border: 1px solid #e2e8f0;
}

.dashboard-table th {
    background: #1e293b;
    color: white;
    font-weight: bold;
    text-transform: uppercase;
}

.dashboard-table tbody tr:nth-child(even) {
    background: #f1f5f9;
}

.dashboard-table .log-id {
    font-weight: bold;
    color: #334155;
}

.success {
    background-color: #34d399;
    color: white;
    font-weight: bold;
    border-radius: 6px;
}

.failure {
    background-color: #f87171;
    color: white;
    font-weight: bold;
    border-radius: 6px;
    cursor: pointer;
}

.failure:hover {
    background-color: #dc2626;
}

@media (max-width: 768px) {
    .dashboard-table {
        font-size: 12px;
    }

    .dashboard-table th,
    .dashboard-table td {
        padding: 8px;
    }
}